body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Welcome {
  margin: 10px;
  padding: 20px;
}

.App {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.App h1, h2, h3, h4 {
  font-family: 'ZCOOL QingKe HuangYou', cursive;
  color: black;
}

.App h2 {
  border-bottom: 1px solid #000;
  padding-bottom: 3px;
}

.App h1 {
  color: gold;
  background: rgb(167, 0, 0);
  padding: 15px;
  margin: 0px;
}

.App body {
  font-family: 'Noto Sans SC', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.MainContainer {
  display: flex;
  flex-flow: row wrap;
  padding: 15px;
}

.SiteMap {
  background: #ccc;
  display: flex;
  flex-flow: wrap row;
  padding: 10px /* Same as Footer */
}

.SiteMap a{
  color: red;
}

.SiteMap div {
  padding-left: 10px;
  padding-right: 10px;
}

.SiteMap ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.Header {
  background: #ccc;
}

.Header a {
  color: #333333;
  font-size: 130%;
  font-weight: bolder;
  padding-left: 15px;
  padding-right: 15px;
}

.Header a:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.Footer {
  background: rgb(167, 0, 0);
  color: gold;
  padding: 10px /* Same as SiteMap */
}

.Footer a {
  color: orange;
}
.Footer .NavButton {
  float: right;
}

.Events {
  margin: 10px;
}

.FAQ {
  margin: 10px;
}

.News {
  margin: 10px;
}

.NewsStory {
  background: rgb(238, 238, 238);
  margin: 10px;
}

.NewsStory h3 {
  background: #888;
  padding: 10px;
}

.NewsStory p {
  text-indent: 2em;
}

.Welcome img {
  float: left;
  height: auto;
  max-width: 30%;
  border-radius: 70%;
  padding: 20px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

